import * as React from "react";

import { Button, Divider, Form, Label, Message } from "semantic-ui-react";
import { computed, observable } from "mobx";
import { inject, observer } from "mobx-react";

import CoreApiService from "../../Services/Core.service";
import { Redirect } from "react-router-dom";
import { RenderingStatus } from "../../Enumerations/OspEnumerations";
import { RootStore } from "../../Stores/RootStore";

@inject("root") @observer
export class TempLogin extends React.Component<{ root?: RootStore }, {}> {
    @observable private emailAddress: string;
    @observable private password: string;

    @observable private requestTempPasswordErrorMessage: string;
    @observable private loginErrorMessage: string;

    @observable private passwordSendingStatus: RenderingStatus;
    @observable private loginStatus: RenderingStatus;

    @computed get formIsValid() {
        let isValid = this.emailAddress && this.emailAddress.indexOf("@") !== -1;
        if (this.passwordSendingStatus === RenderingStatus.Ready) {
            isValid = isValid && !!this.password;
        }
        return isValid;
    }

    public render() {
        if (this.loginStatus === RenderingStatus.Ready) {
            return <Redirect push to="/" />;
        } else {
            return (
                <div>
                    <p>
                        Note: At least one previous successful logon required for requesting a temporary password.
                    </p>
                    <Form error={!!this.requestTempPasswordErrorMessage || !!this.loginErrorMessage}>
                        <Label circular color="orange" size="big">1</Label>
                        <Divider hidden />
                        <Form.Field required>
                            <label>Enter your Corp. email, and request a temporary password</label>
                            <Form.Input
                                onChange={({ }, data: { value?: string }) => this.emailAddress = data.value}
                                error={!!this.requestTempPasswordErrorMessage}
                                disabled={this.passwordSendingStatus === RenderingStatus.Ready} />
                            <Message error content={this.requestTempPasswordErrorMessage} />
                            <Button
                                type="button"
                                primary
                                disabled={!this.formIsValid || this.passwordSendingStatus === RenderingStatus.Ready}
                                loading={this.passwordSendingStatus === RenderingStatus.Waiting}
                                onClick={() => this.requestTempPassword()}>
                                Get password
                            </Button>

                        </Form.Field>
                        {
                            this.passwordSendingStatus === RenderingStatus.Ready &&
                            <div>
                                <Label circular color="orange" size="big">2</Label>
                                <Divider hidden />
                                <Form.Field required>
                                    <label>Stay on this page, check your mailbox, get the password and login</label>
                                    <Form.Input onChange={({ }, data: { value?: string }) => this.password = data.value} />
                                </Form.Field>
                                <Message error content={this.loginErrorMessage} />
                                <Button
                                    type="button"
                                    primary
                                    disabled={!this.formIsValid}
                                    loading={this.loginStatus === RenderingStatus.Waiting}
                                    onClick={() => this.loginWithTempPassword()}>
                                    Login
                                </Button>
                            </div>
                        }
                    </Form>
                </div>
            );
        }
    }

    private async requestTempPassword() {
        try {
            this.passwordSendingStatus = RenderingStatus.Waiting;
            this.requestTempPasswordErrorMessage = undefined;
            await CoreApiService.requestTempPassword(encodeURIComponent(this.emailAddress));
            this.passwordSendingStatus = RenderingStatus.Ready;
        } catch (ex) {
            this.requestTempPasswordErrorMessage = ex.response.data;
            this.passwordSendingStatus = RenderingStatus.Error;
        }
    }

    private async loginWithTempPassword() {
        try {
            this.loginStatus = RenderingStatus.Waiting;
            this.loginErrorMessage = undefined;
            await this.props.root.auth.tempPasswordLogin(this.emailAddress, this.password);
            this.loginStatus = RenderingStatus.Ready;
        } catch (ex) {
            this.loginErrorMessage = ex.response.data;
            this.loginStatus = RenderingStatus.Error;
        }
    }
}



