import * as React from "react";

import { Button, Divider, Grid, Header, Message, Segment } from "semantic-ui-react";

import { Flyout } from "../../Components/Common/Flyout";
import { Root } from "../../Stores/RootStore";
import { TempLogin } from "../../Components/Nav/TempLogin";
import styles from "./Styles/Login.module.less";

export class Login extends React.Component<{}, {}> {
    private tempPasswordFlyout: Flyout;

    public render() {
        return (
            <div className={styles.loginForm}>
                <Grid centered>
                    <Grid.Column width={4}>
                        <Segment color="green">
                            <Message size="huge">
                                <Header size="medium" textAlign="center">O365 Substrate Pulse</Header>
                            </Message>
                            <Divider />
                            <Button primary fluid onClick={() => this.aadLogin()}>
                                Login using Azure AD
                            </Button>
                            <Button primary fluid onClick={() => this.tempPasswordFlyout.openFlyout()}>
                                Login using temporary password
                            </Button>
                        </Segment>
                    </Grid.Column>
                </Grid>
                <Flyout ref={flyout => this.tempPasswordFlyout = flyout} headerText="Temporary password login">
                    <TempLogin />
                </Flyout>
            </div>
        );
    }

    private aadLogin() {
        const url = window.location.href
            .replace(window.location.host + window.location.pathname, window.location.host + "/");

        Root.auth.loginFromAad(url);
    }
}


